<template>
  <div class="flex row width-100" :class="invalid && (((answer === undefined || answer == '') && question.required) || answer.length > 0 && ((answer.length < question.properties.min_length) || (answer.length > question.properties.maxlength))) ? invalid : ''">
    <span class="flex row width-100 max-char" v-if="question.properties.max_length">Caracteres: {{answer.length}}/{{question.properties.max_length}}</span>
    <textarea class="flex row width-100" :id="qid" :name="qid" :rows="question.properties.default_lines" v-model="answer" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" :placeholder="question.properties.placeholder" @changed="update" @invalid="invalid = 'invalid'">
    </textarea>
  </div>
</template>

<script>
export default {
  name: "LongTextField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  data() {
  	return {
  		answer : this.question.properties.default,
      invalid : false
  	};
  },
  mounted() {
    this.update()
  },
  methods: {
    update() {
       this.$emit('update', {
        "section_index": this.section_index, 
        "question_index": this.question_index, 
        "answer": this.answer
      })
    }
  }
};
</script>

<style scoped>
  @media only screen and (min-width: 768px) {
    .max-char {
      margin-top: -24px;
      justify-content: flex-end;
    }
  }
</style>