<template>
	<div v-if="question.properties.style == 'radio'" :class="invalid && (valid === undefined || valid == '') ? invalid : ''">
    <input type="text" oninvalid="setCustomValidity('Isto é um campo obrigatório')" required v-model="valid" onkeydown="return false;" style="caret-color: transparent !important; cursor: default; opacity: 0; width: 0px; height: 0px;" :name="qid + '_validator'" :key="valid" @invalid="invalid = 'invalid'">
    <div class="flex row vca" v-for="(option, index) in question.properties.options" :key="index">
    	<input type="radio" :id="qid + '_o' + index" :name="qid" :value="index" v-model="answer" @change="update">
  		<label class="spaced" :for="qid + '_o' + index">{{option}}</label><br>
  	</div>
    <div class="flex row vca" v-if="question.properties.allowed_other">
      <input type="radio" :id="qid + '_other'" :name="qid" value="other" v-model="answer" @change="update">
      <label class="spaced" :for="qid + '_other'">{{question.properties.other_string}}</label>
      <input type="text" v-if="answer && answer != parseInt(answer)" v-model="other_value" :placeholder="question.properties.placeholder" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" @change="update" :required="question.properties.min_length">
    </div>
    <button type="button" v-if="!question.required" @click="clearSelection">Limpar Seleção</button>
  </div>
  <div v-else :class="invalid && (answer == undefined || answer == '') ? invalid : ''">
		<select :name="qid" :id="qid" :required="question.required" v-model="answer" @invalid="invalid = 'invalid'">
      <option value=""> </option>
			<option v-for="(option, index) in question.properties.options" :key="index" :value="index" :id="qid + '_o' + index" :selected="question.properties.default == index">{{option}}</option>
		</select>
	</div>
</template>

<script>
export default {
  name: "MultipleChoiceField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  data() {
    return {
      answer : this.question.properties.default,
      other_value : "",
      valid : "",
      translation : {
        required : "Isto é um campo obrigatório"
      },
      invalid : ""
    }
  },
  mounted() {
    this.update()
  },
  methods: {
    clearSelection() {
      this.answer = undefined
      this.update()
    },
    update() {
      if (typeof this.answer === 'undefined' && this.question.required) {
        this.valid = ""
      }
      else {
        this.valid = "REQUIRED"
      }
      this.$emit('update', {
        "section_index": this.section_index, 
        "question_index": this.question_index, 
        "answer": this.answer == "other" ? this.other_value : this.answer
      })
    }
  }
};
</script>