<template>
  <div class="flex row" :class="invalid && (answer === undefined || answer == '') ? invalid : ''">
    <input type="date" :id="qid" :name="qid" :min="question.properties.begin" :max="question.properties.end" v-model="answer" @change="update" :required="this.question.required" @invalid="invalid = 'invalid'">
  </div>
</template>

<script>
export default {
  name: "DateField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  data() {
    return {
      answer : this.question.properties.default,
      invalid : false
    }
  },
  mounted() {
  	this.update()
  },
  methods: {
  	update() {
  		 this.$emit('update', {
  			"section_index": this.section_index, 
  			"question_index": this.question_index, 
  			"answer": this.answer
  		})
  	}
  }
};
</script>