<template>
  <div class="flex row" :id="qid">
    {{question.properties.content}}
  </div>
</template>

<script>
export default {
  name: "DescriptionField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  mounted() {
  	this.update()
  },
  methods: {
  	update() {
  		 this.$emit('update', {
  			"section_index": this.section_index, 
  			"question_index": this.question_index
  		})
  	}
  }
};
</script>