<template>
	<div :class="invalid && (valid != ' ') ? invalid : ''">
    <input type="text" oninvalid="setCustomValidity('Por favor confirme a informação introduzida')" :pattern="valid == ' ' ? undefined : '.a'" v-model="valid" class="warning-field removable" :size="valid.length" onkeydown="return false;" :name="qid + '_validator'" :key="valid" v-if="question.required || question.properties.min_selected || question.properties.max_selected" @invalid="invalid = 'invalid'">
	  <div class="flex row vca" v-for="(option, index) in question.properties.options" :key="index">
	  	<input type="checkbox" :id="qid + '_o' + index" :name="qid + '_o' + index" :value="index" v-model="answer" @change="update">
			<label class="spaced" :for="qid + '_o' + index">{{option}}</label>
	  </div>
	  <div v-if="question.properties.allowed_other">
		  <div v-for="(other, index) in others_selected" :key="index" class="removable" :id="qid + '_other' + index + '_parent'">
		  	<input type="checkbox" :id="qid + '_other' + index" :name="qid + '_other' + index" v-model="others_selected[index]" :value="others[index]" @change="add_new(index);">
				<label class="spaced" :for="qid + '_other' + index">{{question.properties.other_string}}</label>
				<input type="text" v-if="others_selected[index]" v-model="others[index]" :placeholder="question.properties.placeholder" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" @change="update" :required="question.properties.min_length">
		  </div>
		</div>
	</div>
</template>

<script>
import nextTick from 'vue'
export default {
  name: "ShortTextField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  data() {
  	return {
  		others: [],
  		others_selected: [false],
  		full: false,
  		mounted : false,
      valid: " ",
      invalid: "",
      answer: typeof this.question.properties.default === 'undefined' ? [] : this.question.properties.default,
      translation: {
        at_least : "Deve selecionar pelo menos ",
        at_most : "Deve selecionar no máximo ",
        options : " opções.",
        mandatoryField : 'Este é um campo obrigatório.'
      }
  	}
  },
  mounted() {
    this.update()
  },
  methods: {
    validate(answer) {
      let selected_normal = 0
      let selected_other = 0
      for (var i = 0; i < answer.length; i++) {
        if (answer[i] === parseInt(answer[i], 10))
          selected_normal++
        else
          selected_other++
      }
      let selected_total = selected_normal + selected_other
      if (answer.length == 0) {
        if (this.question.required)
          return this.translation.mandatoryField
      }
      else {
        if (typeof this.question.properties.min_selected !== 'undefined')
          if (selected_total < this.question.properties.min_selected)
            return this.translation.at_least + this.question.properties.min_selected + this.translation.options

        if (typeof this.question.properties.max_selected !== 'undefined')
          if (selected_total > this.question.properties.max_selected)
            return this.translation.at_most + this.question.properties.max_selected + this.translation.options
      }
      return " "
    },
    update() {
      this.$nextTick(function() {
        let answer = this.answer.concat(this.others)
        this.valid = this.validate(answer)
        this.$emit('update', {
          "section_index": this.section_index, 
          "question_index": this.question_index, 
          "answer": answer,
          "valid" : this.valid
        })
      })
    },
  	async add_new(index) { // typeof this.others[index+1] === 'undefined'
  		if (this.others_selected[index] == true){
        this.others[index] = ""
  			if (typeof this.question.properties.max_other === 'undefined' || index+1 < this.question.properties.max_other) {
	 				this.others_selected.push(false)
	 				const next = index+1
	 				this.$nextTick(async function() {
		        document.getElementById(this.qid + '_other' + next + '_parent').style.opacity = 0
		        await new Promise(r => setTimeout(r, 1))
		        document.getElementById(this.qid + '_other' + next + '_parent').style.opacity = 1
		      })
 				}
 				else
 					this.full = true
  		}
  		else if (this.others_selected.length > 1) {
  			document.getElementById(this.qid + '_other' + index + '_parent').style.opacity = 0
  			await new Promise(r => setTimeout(r, 100))
  			this.others.splice(index, 1)
  			this.others_selected.splice(index, 1)
  			if (this.full) {
	 				this.others_selected.push(false)
	 				this.full = false
  			}
  			document.getElementById(this.qid + '_other' + index + '_parent').style.opacity = 1
  		}

      this.update()
  	}
  }
};
</script>

<style>
	.removable {
    -webkit-transition: opacity 100ms linear;
    transition: opacity 100ms linear;
	}

  @keyframes slideInFromLeft {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

  .warning-field {
    margin-top: -10px;
    animation: 0.2s 0s 1 slideInFromLeft;
    border-width: 0px;
    text-align: center;
    font-weight: 600;
    border-radius: 2px;
    background-color: transparent;
    caret-color: transparent !important; 
    cursor: default;
  }
</style>