<template>
  <img :src="question.properties.content" alt="" :id="qid">
</template>

<script>
export default {
  name: "ImageField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  mounted() {
  	this.update()
  },
  methods: {
  	update() {
  		 this.$emit('update', {
  			"section_index": this.section_index, 
  			"question_index": this.question_index, 
  			"answer": this.answer
  		})
  	}
  }
};
</script>