<template>
  <div class="flex row" :class="invalid && (answer === undefined || answer == '' || answer < question.properties.min || answer > question.properties.max) ? invalid : ''">
    <span class="flex row" v-if="question.properties.style=='slider'" style="margin-right: 20px">
      {{question.properties.prefix}}
      <input type="range" class="spaced"  v-model="answer" :name="qid" :id="qid" :min="question.properties.min" :max="question.properties.max" :step="Math.pow(10, -question.properties.decimal_places)" :required="question.required" @change="update" @invalid="invalid = 'invalid'">
      {{question.properties.suffix}}
    </span>
    <span class="flex row">
      <input type="number" v-model="answer" :name="qid" :id="question.properties.style=='slider' ? qid + '_alt' : qid" :min="question.properties.min" :max="question.properties.max" :step="Math.pow(10, -question.properties.decimal_places)" :required="question.required" @change="update" @invalid="invalid = 'invalid'">
    </span>
  </div>
</template>

<script>
export default {
  name: "NumberField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],    
  data() {
    return {
      answer: this.question.properties.default,
      invalid : false
    };
  },
  mounted() {
    this.update()
  },
  methods: {
    update() {
       this.$emit('update', {
        "section_index": this.section_index, 
        "question_index": this.question_index, 
        "answer": this.answer,
      })
    }
  }
};
</script>

<style scoped>
span.textbox {
      background-color: #FFF;
      color: #888;
      line-height:20px;
      height:20px;
      padding:0px 5px 0px 5px;
      border:1px #888 solid;
      font-size:9pt;
}
  span.textbox input {
      border: 0px;
      background-color: #FFF;
      text-align: right;
  }

  textarea:focus, input:focus{
    outline: none;
}
</style>