<template>
	<h1>{{api_data.title}}</h1>
	<div class="flex row ca">
		<form class="flex column" id="form-area">
			<div v-for="(section, section_index) in api_data.form.sections" :key="section_index">
				<hr>
				<h2><span v-if="!api_data.form.hide_section_numbers">{{section_index+1}}. </span>{{section.section_name}}</h2>
				<div v-for="(question, question_index) in section.questions" :set="qid = 's' + section_index + '_q' + question_index" :key="question_index" class="flex column ca vla no-bottom question">
	    			<label class="question-field" :for="qid"><h3><span v-if="!api_data.form.hide_question_numbers && !section.hide_question_numbers">{{question_index+1}}. </span>{{question.question}}<span v-if="question.required" class="requiredticker">*</span></h3></label>
						<component class="flex column ca vla" :question="question" :question_index="question_index" :section_index="section_index" :qid="qid" :is="question.properties.field_type" @update="updateAnswer"/>
				</div>
			</div>
			{{answers}}
			<div class="flex row ca">
				<input type="submit" value="cancel?">
				<input type="submit" :value="api_data.form.on_submit.button.text">
			</div>
		</form>
	</div>
</template>

<script>
import { Options, Vue } from "vue-class-component";
import axios from "axios";
import * as components from "@/fields"
import { API } from '@/http-common'
import nextTick from 'vue'
export default {
	components,
  name: "FormAnswer",
  data() {
		return {
			api_data: {},
			answers: [],
			validation : []
		};
	},
	created() {
		API
			.get("form/".concat(this.$route.params.id))
			.then(response => (this.initializeData(response)))
			.catch(error => console.log(error.response));
	},
	methods: {
		updateAnswer(value) {
			this.$nextTick(function() {
				this.answers[value.section_index][value.question_index]["answer"] = value["answer"]
				this.validation[value.section_index][value.question_index] = value["valid"]
			})
		},
		initializeData(response) {
			this.api_data = response.data
			console.log(this.api_data.form)
			for (var section in this.api_data.form.sections) {
				this.answers[section] = []
				this.validation[section] = []
				for (var question in this.api_data.form.sections[section].questions) {
					this.validation[section][question] = null
					this.answers[section][question] = {"field_type" : this.api_data.form.sections[section].questions[question].properties["field_type"]}
				}
			}
		}
	}
}
</script>

<style lang="scss">
	#form-area {
		width: 100%;
	}
	.no-bottom {
		padding-bottom: 0;
	}

	.invalid {
    background-color: #e99;
  }

  .requiredticker {
  	color: #b11;
  }
</style>