<template>
  <div class="flex row" :class="invalid && (answer === undefined || answer == '' || answer.length < question.properties.min_length || answer.length > question.properties.maxlength) ? invalid : ''">
  	<!-- <input type="password" :id="qid" :name="qid" v-if="question.properties.sensitive" v-model="answer" :placeholder="question.properties.placeholder" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" @change="update" :required="question.required" @invalid="invalid = 'invalid'"> -->
    <input type="text" :id="qid" :name="qid" v-model="answer" :placeholder="question.properties.placeholder" :minlength="question.properties.min_length" :maxlength="question.properties.max_length" @change="update" :required="question.required" @invalid="invalid = 'invalid'">
  </div>
</template>

<script>
export default {
  name: "ShortTextField",
  props: ["question", "question_index", "section_index", "qid"],
  emits: ['update'],
  data() {
  	return {
  		answer: this.question.properties.default,
      invalid : ""
  	}
  },
  mounted() {
  	this.update()
  },
  methods: {
  	update() {
  		this.$emit('update', {
  			"section_index": this.section_index, 
  			"question_index": this.question_index, 
  			"answer": this.answer
  		})
  	}
  }
};
</script>